@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
.error-page {
  color: #333;
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.error-container {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  padding: 30px;
  box-shadow: 0 4px 8px #0000001a;
}

.error-title {
  color: #e74c3c;
  margin-bottom: 10px;
  font-size: 2.5rem;
}

.error-status {
  margin-bottom: 20px;
  font-size: 1.3rem;
}

.error-description {
  color: #666;
  margin: 20px 0;
  font-size: 1rem;
}

.error-actions {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.btn-home, .btn-retry {
  color: #fff;
  background-color: #3498db;
  border-radius: 5px;
  padding: 12px 25px;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
}

.btn-home:hover, .btn-retry:hover {
  background-color: #2980b9;
}

@media (width >= 768px) {
  .error-page {
    padding: 0 20px;
  }

  .error-container {
    max-width: 800px;
    padding: 50px;
  }

  .error-title {
    font-size: 3rem;
  }

  .error-status {
    font-size: 1.5rem;
  }

  .error-description {
    font-size: 1.2rem;
  }
}

.offers-container {
  text-align: center;
  padding: 40px 10%;
}

.offers-title {
  color: #333;
  margin-bottom: 40px;
  font-size: 2.5rem;
}

.offer-card-container {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  margin-top: 20px;
  display: flex;
}

.offer-card {
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 300px;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

.offer-card:hover {
  box-shadow: 0 6px 15px #00000026;
}

.offer-image {
  object-fit: cover;
  border-bottom: 1px solid #e0e0e0;
  width: 100%;
  height: 200px;
}

.offer-details {
  text-align: left;
  padding: 20px;
}

.offer-name {
  margin-bottom: 10px;
  font-size: 1.5rem;
  font-weight: bold;
}

.offer-description {
  color: #555;
  margin-bottom: 10px;
  font-size: 1rem;
}

.offer-validity {
  color: #888;
  margin-bottom: 15px;
  font-size: .9rem;
}

.offer-button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s;
}

.offer-button:hover {
  background-color: #2980b9;
}

@media (width <= 768px) {
  .offers-container {
    padding: 40px 5%;
  }

  .offer-card-container {
    flex-direction: column;
    align-items: center;
  }

  .offer-card {
    max-width: 100%;
    margin-bottom: 20px;
  }
}

.help-container {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 90vh;
  margin-bottom: 50px;
  padding: 20px;
  display: flex;
}

.help-content {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  padding: 20px;
}

.help-title {
  color: #333;
  text-align: center;
  margin-bottom: 20px;
  font-size: 2em;
}

.help-description {
  color: #555;
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.2em;
}

.faq-title {
  color: #333;
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.8em;
}

.faq-container {
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
}

.faq-item {
  border-bottom: 1px solid #ddd;
}

.faq-item:last-child {
  border-bottom: none;
}

.faq-question {
  text-align: left;
  cursor: pointer;
  background-color: #f9f9f9;
  border: none;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 15px;
  font-size: 1em;
  font-weight: 500;
  transition: background-color .3s;
  display: flex;
}

.faq-question:hover {
  background-color: #f0f0f0;
}

.faq-answer {
  color: #555;
  padding: 15px;
  font-size: 1em;
  display: none;
}

.faq-answer.show {
  display: block;
}

.accordian {
  cursor: pointer;
  border-bottom: 2px solid #dedede;
  width: 70vw;
  margin: 30px 0;
  padding: 4px;
}

.accordian-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.accordian-title {
  font-weight: bold;
}

.resMenuCard {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 15vh;
  margin: 30px 0;
  padding: 0 10px;
  display: flex;
}

.resMenuCardDetails {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 80%;
  display: flex;
}

.nameItem {
  width: 100%;
  font-size: 20px;
  font-weight: 700;
}

.priceItem {
  width: 100%;
  font-size: 17px;
  font-weight: 600;
}

.descItem {
  width: 100%;
  font-size: 12px;
  font-weight: 400;
}

.resMenuCardImg {
  justify-content: flex-end;
  align-items: center;
  width: 20%;
  display: flex;
}

.resMenuCardImg img {
  border-radius: 10px;
  width: 80px;
}

.res-menu-btn {
  color: green;
  cursor: pointer;
  background-color: #fff;
  border: #0000;
  border-radius: 5px;
  margin: 90px 10px 0;
  padding: 2px 10px;
  font-weight: 600;
  position: absolute;
  box-shadow: 0 4px 8px #0000001a;
}

@media (width <= 768px) {
  .resMenuDetailCard {
    flex-direction: column;
    align-items: flex-start;
  }

  .resMenuDetailTitle, .resMenuDetailCuisines, .resMenuDetailOutlet {
    font-size: 14px;
  }

  .footer-menu-item {
    flex-direction: column;
    align-items: flex-start;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

body {
  width: 100%;
  height: 100%;
}

.header {
  background-color: #2d2d2d;
  justify-content: space-around;
  align-items: center;
  height: 10vh;
  display: flex;
}

.logo {
  justify-content: center;
  align-items: center;
  width: 120px;
  padding: 10px;
  display: flex;
}

.logo img {
  object-fit: cover;
  width: 100%;
}

.nav ul {
  gap: 20px;
}

.nav ul li {
  list-style: none;
}

.nav ul li a {
  color: #fff;
  font-weight: 400;
  text-decoration: none;
}

.nav ul li a:hover {
  text-decoration: underline;
}

.nav-items {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-bar {
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 50px;
  margin: 10px;
  padding: 10px;
  display: flex;
}

#search {
  border: .4px solid #000;
  border-radius: 10px;
  width: 30vmax;
  padding: 5px 20px;
}

.cart {
  justify-content: center;
  align-items: center;
  display: flex;
}

.cart a {
  text-decoration: none;
  list-style: none;
}

.cart-btn {
  color: #fff;
  cursor: pointer;
}

.cart-items {
  color: #fff;
  cursor: pointer;
  padding: 0 1px;
  font-size: large;
  font-weight: 500;
}

.cart-button {
  justify-content: center;
  align-items: center;
  display: flex;
}

.loginBtn {
  cursor: pointer;
  color: #fff;
  background-color: #2d2d2d;
  border: .5px solid #2d2d2d;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  font-size: 1vw;
  font-weight: 500;
  display: flex;
}

.search {
  cursor: pointer;
}

.searchBtn {
  background-color: #fff;
  border: #0000;
  padding: 2px;
}

.rest-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.rest-container a {
  color: #2d2d2d;
  text-decoration: none;
}

.card {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 1rem;
  flex-direction: column;
  width: 220px;
  min-height: 280px;
  margin: .75rem;
  padding: 1rem;
  transition: all .3s;
  display: flex;
  box-shadow: 0 2px 6px #0000000d;
}

.card:hover {
  cursor: pointer;
  border-color: #b0b0b0;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #00000026;
}

.rest-img img {
  object-fit: cover;
  border-radius: .75rem;
  width: 100%;
  height: 130px;
}

.rest-details {
  flex-direction: column;
  flex-grow: 1;
  gap: .5rem;
  margin-top: .75rem;
  display: flex;
}

.rest-name {
  color: #333;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1.2;
  overflow: hidden;
}

.cuisine {
  color: #666;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: .95rem;
  font-weight: 400;
  overflow: hidden;
}

.rest-rating-number {
  color: #444;
  align-items: center;
  font-size: .9rem;
  font-weight: 500;
  display: flex;
}

.location {
  color: #777;
  font-size: .9rem;
  font-weight: 500;
}

.nav-items-sidebar {
  z-index: 999;
  backdrop-filter: blur(4px);
  background-color: #ffffffd4;
  flex-direction: column;
  gap: 20px;
  width: 300px;
  height: 100%;
  padding: 50px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-items-sidebar li {
  list-style: none;
}

.nav-items-sidebar li a {
  color: #000;
  padding: 5px 10px;
  font-weight: 400;
  text-decoration: none;
}

.nav-items-sidebar li a:hover {
  color: #fff;
  background-color: #0008;
  text-decoration: underline;
}

.menu-btn {
  display: none;
}

.filter {
  justify-content: center;
  align-items: center;
  height: 7vh;
  display: flex;
}

.filter-btn {
  cursor: pointer;
  background-color: #fff;
  border: .5px solid #2d2d2d;
  border-radius: 40px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1vw;
  font-weight: 500;
}

.filter-btn:hover {
  color: #fff;
  background-color: #2d2d2d;
  transition: all .2s;
}

.cuisine-dropdown {
  position: relative;
}

.cuisine-options {
  z-index: 1;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: absolute;
  top: 100%;
  left: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.cuisine-btn {
  cursor: pointer;
  text-align: left;
  background-color: #0000;
  border: none;
  width: 100%;
  padding: 8px 12px;
  display: block;
}

.cuisine-btn:hover {
  background-color: #f0f0f0;
}

.shimmer-header {
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 20vh;
  padding: 20px;
  display: flex;
}

.shimmer-header input, .shimmer-header button {
  background-color: #8080803c;
  border-radius: 25px;
}

.shimmer-header button {
  padding: 18px 60px;
}

.shimmer-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 20px;
  display: flex;
}

.shimmer-card {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  border-radius: 12px;
  width: 240px;
  height: 300px;
  padding: 15px;
  animation: 1.5s linear infinite shimmer;
  overflow: hidden;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }

  100% {
    background-position: 200% 0;
  }
}

.shimmer-animated {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  animation: 1.5s linear infinite shimmer;
}

.shimmer-header-menu {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 10px;
  display: flex;
}

.shimmer-card-menu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.shimmer-res-title {
  background-color: #8080803c;
  background-size: 200% 100%;
  border: #0000;
  border-radius: 10px;
  width: 40px;
  padding: 20px 70px;
  font-weight: 700;
  animation: 1.5s linear infinite shimmer;
}

.shimmer-card-items {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60vw;
  height: 20vh;
  margin: 10px;
  padding: 15px;
  animation: 1.5s linear infinite shimmer;
  display: flex;
}

.menu-items-parent {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.shimmer-container-menu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 70vw;
  height: 100vh;
  display: flex;
}

.shimmer-item-card-menu {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  border-radius: 10px;
  width: 100%;
  height: 18vh;
  animation: 1.5s linear infinite shimmer;
}

.resMenu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.resMenuName {
  color: #151515;
  font-weight: 700;
}

.resMenuDetailCard {
  border: #0000;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 60vw;
  height: 20vh;
  margin: 10px;
  padding: 15px;
  display: flex;
  box-shadow: 0 1px 3px #0000001f, 0 1px 2px #0000003d;
}

.resMenuDetailTitle {
  align-items: center;
  font-weight: 500;
  display: flex;
}

.resMenuDetailCuisines {
  color: #a91d3a;
  font-weight: 600;
}

.resMenuDetailOutlet {
  color: #151515;
  font-weight: 600;
}

.resMenuDetailOutlet span {
  font-weight: 400;
}

.line {
  background-color: #2d2d2d;
  width: 70%;
  height: .3px;
}

.footer-menu-item {
  text-align: center;
  background-color: #8080803c;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  width: 50vw;
  height: 15vh;
  display: flex;
}

.footer-menu-item h4 {
  font-weight: 500;
}

.footer-menu-item h5 {
  font-weight: 400;
}

.footer-menu-item h6 {
  font-weight: 300;
}

.contact-container {
  background-color: #fff;
  border-radius: 10px;
  max-width: 900px;
  margin: 0 auto;
  padding: 15px 30px;
}

.contact-title {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 2em;
}

.contact-info {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  display: grid;
}

.contact-item {
  text-align: center;
  background-color: #f9f9f9;
  border-radius: 10px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.contact-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px #0003;
}

.contact-item h2 {
  color: #000;
  margin-bottom: 10px;
  font-size: 1.5em;
}

.contact-item p {
  color: #555;
  margin: 5px 0;
  font-size: 1em;
}

.offline-container {
  text-align: center;
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  display: flex;
}

.offline-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.offline-title {
  color: #333;
  margin-bottom: 10px;
  font-size: 2rem;
}

.offline-message {
  color: #666;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.retry-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
}

.retry-button:hover {
  background-color: #0056b3;
}

.veglabel {
  color: #fff;
  background-color: green;
  border-radius: 10px 0 0;
  margin: 15.5px 0 0 15.5px;
  padding: 2px 10px;
  font-weight: 400;
  position: absolute;
}

.payment-page {
  color: #2c3e50;
  background: #fff;
  border-radius: 12px;
  max-width: 480px;
  margin: 50px auto;
  padding: 30px 35px;
  font-family: Poppins, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  box-shadow: 0 12px 25px #0000001a;
}

.payment-page h1 {
  color: #34495e;
  text-align: center;
  letter-spacing: 1px;
  margin-bottom: 30px;
  font-size: 2.5rem;
  font-weight: 700;
}

.payment-page h2 {
  color: #2980b9;
  border-bottom: 3px solid #3498db;
  margin-bottom: 25px;
  padding-bottom: 10px;
  font-size: 1.6rem;
  font-weight: 600;
}

.payment-page h3 {
  color: #27ae60;
  margin-top: 15px;
  font-weight: 700;
}

.payment-page form {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.payment-page label {
  color: #34495e;
  user-select: none;
  margin-bottom: 8px;
  font-size: 1.1rem;
  font-weight: 600;
}

.payment-page input[type="text"], .payment-page input[type="password"], .payment-page input[type="month"] {
  border: 2px solid #bdc3c7;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: 12px 15px;
  font-size: 1rem;
  transition: all .3s;
  box-shadow: inset 0 1px 3px #0000001a;
}

.payment-page input[type="text"]:focus, .payment-page input[type="password"]:focus, .payment-page input[type="month"]:focus {
  background-color: #f0f8ff;
  border-color: #3498db;
  box-shadow: 0 0 8px #3498db80;
}

.payment-methods {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 25px;
  margin-bottom: 30px;
  display: flex;
}

.payment-methods label {
  cursor: pointer;
  user-select: none;
  background: #ecf0f1;
  border-radius: 10px;
  flex: 120px;
  align-items: center;
  gap: 10px;
  padding: 12px 20px;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .3s, box-shadow .3s;
  display: flex;
}

.payment-methods input[type="radio"] {
  accent-color: #2980b9;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.payment-methods label:hover {
  background-color: #d6e9ff;
  box-shadow: 0 4px 12px #2980b94d;
}

.payment-page button[type="submit"] {
  color: #fff;
  cursor: pointer;
  user-select: none;
  background-color: #2980b9;
  border: none;
  border-radius: 12px;
  width: 100%;
  padding: 15px 0;
  font-size: 1.25rem;
  font-weight: 700;
  transition: background-color .3s, box-shadow .3s;
  box-shadow: 0 8px 18px #2980b980;
}

.payment-page button[type="submit"]:hover {
  background-color: #1c5980;
  box-shadow: 0 12px 22px #1c5980b3;
}

.payment-page p {
  color: #2c3e50;
  user-select: none;
  margin: 6px 0;
  font-size: 1rem;
}

@media (width <= 520px) {
  .payment-page {
    margin: 25px 15px;
    padding: 25px 20px;
  }

  .payment-methods {
    flex-direction: column;
  }

  .payment-methods label {
    flex: auto;
    justify-content: flex-start;
  }
}

@media (width <= 1000px) {
  .rest-name {
    font-size: 2vw;
  }

  .cuisine {
    font-size: 1.6vw;
  }

  .rest-rating-number, .location {
    font-size: 1.5vw;
  }

  .resMenuCard {
    margin: 20px 0;
  }
}

@media (width <= 880px) {
  .filter-btn {
    font-size: 2vw;
  }

  .nav ul li {
    font-size: 1.8vw;
  }

